.tool-bar {
  position: absolute;
  z-index: 1;
  right: 30px;
  top: 10px;
}
.tool-bar .button {
  min-width: 24px;
  height: 28px;
  margin-right: 5px;
  display: inline-block;
  cursor: pointer;
  font-size: 14px;
  line-height: 28px;
  text-align: center;
  color: #999;
}
.tool-bar .button:hover {
  color: #333;
}

.rc-md-editor {
  height: 100%;
  min-height: 0;
  padding-bottom: 1px;
  position: relative;
  border: 1px solid #ddd;
  background: #fff;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}
.rc-md-editor.full {
  width: 100%;
  height: 100% !important;
  position: fixed;
  left: 0px;
  top: 0px;
  z-index: 1000;
}
.rc-md-editor .ctrl-wrap {
  position: absolute;
  top: 0;
  right: 0;
}
.rc-md-editor .ctrl-wrap button {
  line-height: 24px;
  background: #fff;
  border: none;
  border: 1px solid #d9d9d9;
}
.rc-md-editor .ctrl-wrap button.checked {
  border: 1px solid #1890FF;
  color: #1890FF;
}
.rc-md-editor .editor-container {
  flex: 1 1;
  display: flex;
  width: 100%;
  min-height: 0;
}
.rc-md-editor .editor-container .sec-md {
  flex: 1 1;
  position: relative;
  min-height: 0;
  min-width: 0;
}
.rc-md-editor .editor-container .sec-md .input {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 20px 15px 15px 15px;
  overflow-y: scroll;
  border: none;
  resize: none;
  outline: none;
  min-height: 0;
  background: #fff;
  color: #333;
  font-size: 14px;
  line-height: 1.7;
}
.rc-md-editor .editor-container .sec-html {
  flex: 1 1;
  position: relative;
  min-height: 0;
  min-width: 0;
  margin-left: 20px;
}
.rc-md-editor .editor-container .sec-html .html-wrap {
  height: 100%;
  box-sizing: border-box;
  padding-top: 20px;
  padding-right: 15px;
  padding-bottom: 15px;
  overflow: auto;
}
.rc-md-editor .editor-container .sec-html .html-code-wrap {
  height: 100%;
  box-sizing: border-box;
  padding-top: 20px;
}
.rc-md-editor .editor-container .sec-html .html-code {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 20px 15px 15px 15px;
  border: none;
  resize: none;
  outline: none;
  min-height: 200px;
  background: #fff;
  color: #333;
  font-size: 14px;
  line-height: 20px;
}
.rc-md-navigation {
  min-height: 38px;
  padding: 0px 8px;
  box-sizing: border-box;
  border-bottom: 1px solid #ddd;
  font-size: 16px;
  background: #f1f1f1;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.rc-md-navigation .navigation-nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #666;
}
.rc-md-navigation .button-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.rc-md-navigation .button-wrap .button {
  position: relative;
  min-width: 24px;
  height: 28px;
  margin-right: 5px;
  display: inline-block;
  cursor: pointer;
  line-height: 28px;
  text-align: center;
  color: #999;
}
.rc-md-navigation .button-wrap .button:hover {
  color: #333;
}
.rc-md-navigation ul,
.rc-md-navigation li {
  list-style: none;
  margin: 0;
  padding: 0;
}
.rc-md-navigation h1,
.rc-md-navigation h2,
.rc-md-navigation h3,
.rc-md-navigation h4,
.rc-md-navigation h5,
.rc-md-navigation h6,
.rc-md-navigation .h1,
.rc-md-navigation .h2,
.rc-md-navigation .h3,
.rc-md-navigation .h4,
.rc-md-navigation .h5,
.rc-md-navigation .h6 {
  font-family: inherit;
  font-weight: 500;
  color: inherit;
  padding: 0;
  margin: 0;
  line-height: 1.1;
}
.rc-md-navigation h1 {
  font-size: 34px;
}
.rc-md-navigation h2 {
  font-size: 30px;
}
.rc-md-navigation h3 {
  font-size: 24px;
}
.rc-md-navigation h4 {
  font-size: 18px;
}
.rc-md-navigation h5 {
  font-size: 14px;
}
.rc-md-navigation h6 {
  font-size: 12px;
}
.custom-html-style {
  color: #333;
}
.custom-html-style h1 {
  font-size: 32px;
  padding: 0px;
  border: none;
  font-weight: 700;
  margin: 32px 0;
  line-height: 1.2;
}
.custom-html-style h2 {
  font-size: 24px;
  padding: 0px 0;
  border: none;
  font-weight: 700;
  margin: 24px 0;
  line-height: 1.7;
}
.custom-html-style h3 {
  font-size: 18px;
  margin: 18px 0;
  padding: 0px 0;
  line-height: 1.7;
  border: none;
}
.custom-html-style p {
  font-size: 14px;
  line-height: 1.7;
  margin: 8px 0;
}
.custom-html-style a {
  color: #0052d9;
}
.custom-html-style a:hover {
  text-decoration: none;
}
.custom-html-style strong {
  font-weight: 700;
}
.custom-html-style ol,
.custom-html-style ul {
  font-size: 14px;
  line-height: 28px;
  padding-left: 36px;
}
.custom-html-style li {
  margin-bottom: 8px;
  line-height: 1.7;
}
.custom-html-style hr {
  margin-top: 20px;
  margin-bottom: 20px;
  border: 0;
  border-top: 1px solid #eee;
}
.custom-html-style pre {
  display: block;
  background-color: #f5f5f5;
  padding: 20px;
  font-size: 14px;
  line-height: 28px;
  border-radius: 0;
  overflow-x: auto;
  word-break: break-word;
}
.custom-html-style code {
  background-color: #f5f5f5;
  border-radius: 0;
  padding: 3px 0;
  margin: 0;
  font-size: 14px;
  overflow-x: auto;
  word-break: normal;
}
.custom-html-style code:after,
.custom-html-style code:before {
  letter-spacing: 0;
}
.custom-html-style blockquote {
  position: relative;
  margin: 16px 0;
  padding: 5px 8px 5px 30px;
  background: none repeat scroll 0 0 rgba(102, 128, 153, 0.05);
  border: none;
  color: #333;
  border-left: 10px solid #D6DBDF;
}
.custom-html-style img,
.custom-html-style video {
  max-width: 100%;
}
.custom-html-style table {
  font-size: 14px;
  line-height: 1.7;
  max-width: 100%;
  overflow: auto;
  border: 1px solid #f6f6f6;
  border-collapse: collapse;
  border-spacing: 0;
  box-sizing: border-box;
}
.custom-html-style table td,
.custom-html-style table th {
  word-break: break-all;
  word-wrap: break-word;
  white-space: normal;
}
.custom-html-style table tr {
  border: 1px solid #efefef;
}
.custom-html-style table tr:nth-child(2n) {
  background-color: transparent;
}
.custom-html-style table th {
  text-align: center;
  font-weight: 700;
  border: 1px solid #efefef;
  padding: 10px 6px;
  background-color: #f5f7fa;
  word-break: break-word;
}
.custom-html-style table td {
  border: 1px solid #efefef;
  text-align: left;
  padding: 10px 15px;
  word-break: break-word;
  min-width: 60px;
}

