// antd 菜单栏下拉样式
.nav-common-sub.ant-menu-dark {
  // 下拉样式
  .ant-menu-sub {
    background: #404041;
    // 标题
    .ant-menu-item-group-title {
      position: relative;
      color: #fff;
      background: #404041; //2a2a2a
      overflow: hidden;
      // border-bottom: 1px dashed #aaa;
      // 虚线
      // background-image: linear-gradient(to right, #aaa 0%, #aaa 50%, transparent 50%);
      // background-size: 11px 1px;
      // background-repeat: repeat-x;
      // background-position: 10px bottom;
      .text{
        position: relative;
        display: inline-block;
        padding: 0 10px 0 10px;
        background: #404041;
        z-index: 2;
      }
      .line::before{
        content: '';
        position: absolute;
        width: 9999px;
        height: 1px;
        background: #aaa;
        top: 50%;
        left: -50px;
        z-index:1;
      }
    }
    // 下拉菜单标题
    .ant-menu-submenu-title {
      background: #404041; //2a2a2a
    }
    .ant-menu-item {
      // background:#191919;
    }
    .ant-menu-item:hover{
      color:#47a3da;
      background: #2f2f2f;
    }
    .ant-menu-item-selected{
      background: #404041;
      color: rgba(255, 255, 255, 0.65);
    }
    li {
      border-bottom: #000;
    }
    &.ant-menu-sub {
      -webkit-box-shadow: 0 2px 8px rgba(25, 25, 25, 0.45) inset;
      box-shadow: 0 2px 8px rgba(25, 25, 25, 0.45) inset;
    }
  }
}
